@image-system-path : "/images/front/";
@font-system-path : "/fonts/";
@font-face {
  font-family: 'montserratbold';
  src: url('@{font-system-path}montserrat-bold-webfont.eot');
  src: url('@{font-system-path}montserrat-bold-webfont.eot?#iefix') format('embedded-opentype'), url('@{font-system-path}montserrat-bold-webfont.woff2') format('woff2'), url('@{font-system-path}montserrat-bold-webfont.woff') format('woff'), url('@{font-system-path}montserrat-bold-webfont.ttf') format('truetype'), url('@{font-system-path}montserrat-bold-webfont.svg#montserratbold') format('svg');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'FuturaBT-Medium';
  src: url('@{font-system-path}FuturaBT-Medium_gdi.eot');
  src: url('@{font-system-path}FuturaBT-Medium_gdi.eot?#iefix') format('embedded-opentype'), url('@{font-system-path}FuturaBT-Medium_gdi.woff') format('woff'), url('@{font-system-path}FuturaBT-Medium_gdi.ttf') format('truetype'), url('@{font-system-path}FuturaBT-Medium_gdi.svg#FuturaBT-Medium') format('svg');
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  unicode-range: U+0020-25CA;
}
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li,
label,
figure {
  margin: 0;
  padding: 0;
  overflow: hidden;
}
* {
  margin: 0;
  padding: 0;
}
html:embed {
  display: none;
}
a,
a:active,
a:focus {
  outline: none;
}
/*
END RESET & CLEARFIX
*/
/*
2. BASICS (BODY, HEADERS, LINKS, ETC)
*/

html {
  height: 100%;
  margin: 0px;
  padding: 0px;
}
ul,
li {
  list-style-type: none;
}
body {
  font: 14px/18px Verdana, Arial, Helvetica, sans-serif;
  color: #fff;
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  width: 100%;
  float: left;
  display: block;
  background-color: #000;
}
.no-gutter [class*='col-'] {
  padding-right: 0;
  padding-left: 0;
}
.row.no-gutter {
  margin-left: 0 !important;
  margin-right: 0 !important;
}
/*
END BASICS (BODY, HEADERS, LINKS, ETC)
*/
/*
3. WRAPER
*/

#wraper {
  height: 100%;
  box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
}
.loader {
  width: 100%;
  height: 100%;
  background: url('@{image-system-path}loader.gif') no-repeat 50% 50% #000;
  position: fixed;
  display: block;
  z-index: 10000000;
}
/*
4. HEADER
*/
/*
	4.1. TOP-BAR
*/

#top-bar {
  overflow: hidden;
  position: fixed;
  width: 100%;
  z-index: 10;
}
#top-bar h1 {
  float: left;
}
#top-bar h1 a {
  display: block;
  background-image: url('@{image-system-path}logo.jpg');
  background-size: cover;
  margin-left: 30px;
  background-color: #F60600;
  text-decoration: none;
  text-indent: -9999px;
  overflow: hidden;
  height: 111px;
  margin-top: 0px;
  float: left;
  width: 105px;
}
#top-bar div {
  width: auto;
}
#top-bar div + div {
  height: 50px;
  background-color: #fcfbfb;
}
#top-bar div + div + div {
  width: 19%;
  text-align: center;
  padding: 0;
}
#top-bar div + div + div label {
  border-left: 1px solid #222222;
  display: block;
  padding: 13px 30px 0 30px;
  height: 100%;
  overflow: hidden;
}
#top-bar div + div + div label span {
  display: block;
  background-color: #3a3a3a;
  padding: 2px;
  height: 25px;
}
#top-bar div + div + div label input {
  float: left;
  border: 0;
  background-color: transparent;
}
#top-bar div + div + div label input.submit {
  width: 20px;
  -webkit-appearance: none;
}
#top-bar ul li {
  width: 16%;
}
#top-bar ul li a {
  display: block;
  padding: 16px 0;
  text-align: center;
  text-decoration: none;
  color: #fff;
  border-right: 0px solid #222222;
  font-size: 15px;
  text-transform: uppercase;
  font-family: 'Lato', sans-serif;
  font-weight: normal;
}
#top-bar > nav {
  background: transparent none repeat scroll 0 0;
  float: right;
  margin-right: 30px;
}
#top-bar ul li a:hover {
  color: #aa0000;
}
#top-bar div p {
  float: left;
  margin: 16px 10px 0 10px;
  font-size: 12px;
  color: #c9c9c9;
}
#top-bar h1,
#top-bar div,
#top-bar ul li {
  float: left;
  margin-top: -50px;
  -webkit-transition: all 0.4s ease-in-out;
  -moz-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}
.animate #top-bar h1,
.animate #top-bar div,
.animate #top-bar ul li {
  margin-top: 0;
}
.animate #top-bar h1 {
  -webkit-transition-delay: 1s;
  -moz-transition-delay: 1s;
}
.animate #top-bar li {
  -webkit-transition-delay: 1.4s;
  -moz-transition-delay: 1.4s;
}
.animate #top-bar li + li {
  -webkit-transition-delay: 1.6s;
  -moz-transition-delay: 1.6s;
}
.animate #top-bar li + li + li {
  -webkit-transition-delay: 1.8s;
  -moz-transition-delay: 1.8s;
}
.animate #top-bar div + div {
  -webkit-transition-delay: 2s;
  -moz-transition-delay: 2s;
}
.animate #top-bar div + div + div {
  -webkit-transition-delay: 2.2s;
  -moz-transition-delay: 2.2s;
}
/*
	END TOP-BAR
*/
/*
	4.2. MENU
*/

#menumobile {
  -webkit-transition: all 0.8s ease-in-out;
  -moz-transition: all 0.8s ease-in-out;
  -o-transition: all 0.8s ease-in-out;
  transition: all 0.8s ease-in-out;
}
#menumobile ul li a {
  display: block;
  padding: 16px 0;
  text-align: center;
  background-color: #fcfbfb;
  text-decoration: none;
  color: #000;
  border-right: 0px solid #222222;
  font-size: 16px;
  text-transform: uppercase;
  font-family: 'Lato', sans-serif;
  font-weight: 700;
}
#menumobile ul li a {
  background: none;
}
#menumobile ul li {
  float: left;
  height: 50px;
  margin-top: -50px;
  -webkit-transition: all 0.4s ease-in-out;
  -moz-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}
#menumobile ul li {
  margin-top: 0;
}
#menumobile ul li {
  -webkit-transition-delay: 1.4s;
  -moz-transition-delay: 1.4s;
}
.overlay nav {
  font-size: 54px;
  height: 60%;
  position: relative;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
}
.overlay {
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
}
.overlay ul {
  display: inline-block;
  height: 100%;
  list-style: outside none none;
  margin: 0 auto;
  padding: 0;
  position: relative;
}
.overlay ul li {
  display: block;
  height: calc(100% / 5);
  min-height: 54px;
}
.overlay ul li a {
  color: #000;
  display: block;
  font-weight: 300;
  transition: color 0.2s ease 0s;
  line-height: 70px;
  font-family: FuturaBT-Medium;
  text-decoration: none;
}
.bt-menu-trigger {
  background: rgba(0, 0, 0, 0.5) none repeat scroll 0 0;
  cursor: pointer;
  display: block;
  height: 40px;
  position: fixed;
  right: 50px;
  top: 25px;
  width: 45px;
  z-index: 100;
}
.bt-menu-trigger span::before {
  transform: translateY(-250%);
}
.bt-menu-trigger span::before,
.bt-menu-trigger span::after {
  background: #fff none repeat scroll 0 0;
  content: "";
  height: 100%;
  position: absolute;
  transition: transform 0.3s ease 0s, background-color 0.3s ease 0s;
  width: 30px;
}
.bt-menu-trigger span::after {
  transform: translateY(250%);
}
.bt-menu-trigger span {
  -moz-user-select: none;
  background-color: #fff;
  display: block;
  font-size: 0;
  height: 8%;
  left: 8px;
  position: absolute;
  top: 18px;
  transition: background-color 0.3s ease 0s;
  width: 30px;
}
.bt-menu-trigger {
  cursor: pointer;
}
/*
	END MENU
*/
/*
	4.3. SOCIAL MEDIA NAVIGATION
*/

