/*
	END SOCIAL MEDIA NAVIGATION
*/
/*
END HEADER
*/
/*
5. PORTFOLIO
*/
/*
	5.1. PORTFOLIO THUMBS
*/

@media (max-width: 810px) {
  .posts  {
    overflow: scroll !important;
    position: relative;
    height: 100% !important;
  }
  #contactus, #aboutus{
    overflow: scroll !important;
    height: 100% !important;
  }
}

.posts  {
  overflow: hidden;
  position: relative;
  height: 100% !important;
}
#contactus, #aboutus{
  overflow: hidden;
  height: 100%;
}
#thumbnails {
  clear: both;
  background: #000;
  z-index: 10;
}
#thumbnails * {
  -webkit-transition: all 0.8s ease-in-out;
  -moz-transition: all 0.8s ease-in-out;
  -o-transition: all 0.8s ease-in-out;
  transition: all 0.8s ease-in-out;
}
/*
	END PORTFOLIO THUMBS
*/
/*
	5.2. PORTFOLIO BOXES
*/

figure {
  background-position: center center;
  background-size: cover;
  box-shadow: 0 0 0 1000px rgba(0, 0, 0, 0.2) inset;
}
figure:hover {
  padding: 0;
  cursor: pointer;
  box-shadow: 0 0 0 1000px rgba(0, 0, 0, 0) inset;
}

figure.mainImage:hover {
  box-shadow: none;
}

.hiddenscroll {
  overflow: auto!important;
  width: 100%;
  -webkit-overflow-scrolling: touch;
}
div.bigerImage {
  height: 100% !important;
  -webkit-transition-delay: 0.1s;
  -moz-transition-delay: 0.1s;
}
.bigerImage .big {
  width: 100%;
  height: 100% !important;
  z-index: 100;
  -webkit-transition-delay: 0.1s;
  -moz-transition-delay: 0.1s;
}
.ColumnLOne {
  left: 0;
}
.ColumnLTwo {
  left: 25%;
}
.ColumnLThree {
  left: 50%;
}
.ColumnRTwo {
  right: 25%
}
.ColumnROne {
  right: 0;
}
.row {
  position: relative;
  overflow: hidden;
}
/*
	END PORTFOLIO BOXES
*/
/*
	5.3. ANIMATIONS
*/

.smallimg {
  position: fixed;
  top: 80px;
  cursor: pointer;
  left: -600px;
  z-index: 2147483647;
  color: #FFF;
  background: #ff303b;
  float: left;
  display: block;
  padding: 6px 10px 7px;
  opacity: 1;
  -webkit-transition: all 3s ease-in-out;
  -moz-transition: all 3s ease-in-out;
  -o-transition: all 3s ease-in-out;
  transition: all 3s ease-in-out;
  font-family: 'Lato', sans-serif;
  font-weight: 600;
  color: #FFF;
  font-size: 14px;
  text-transform: uppercase;
  text-decoration: none;
}
.smallimg.leftpos {
  left: 20px;
}
.smallimg.newpos {
  left: -600px!important;
}
div.bigerImage .ColumnRTwo {
  right: -50%;
}
div.bigerImage .ColumnROne {
  right: 0%;
  -webkit-transition-delay: 0.3s;
  -moz-transition-delay: 0.3s;
}
div.bigerImage[data-id="three"] .ColumnROne {
  right: -100%;
  -webkit-transition-delay: 0.3s;
  -moz-transition-delay: 0.3s;
}
figure {
  top: 0px;
  -webkit-transition-delay: 4s;
  -moz-transition-delay: 4s;
}
div.bottomRow figure {
  top: 100%;
}
div.bottomRow figure + figure {
  -webkit-transition-delay: 0.1s;
  -moz-transition-delay: 0.1s;
}
div.bottomRow figure + figure + figure {
  -webkit-transition-delay: 0.2s;
  -moz-transition-delay: 0.2s
}
div.bottomRow figure + figure + figure + figure {
  -webkit-transition-delay: 0.3s;
  -moz-transition-delay: 0.3s
}
figure {
  -webkit-transition: all 0.8s ease-in-out;
  -moz-transition: all 0.8s ease-in-out;
  -o-transition: all 0.8s ease-in-out;
  transition: all 0.8s ease-in-out;
}
div.bottomRownew figure {
  top: 100%;
}
figure .contentRight {
  width: 65%;
  position: absolute;
  z-index: 1111;
  background: #FFF;
  right: -150%;
  -webkit-transition: all 1s ease-in-out;
  -moz-transition: all 1s ease-in-out;
  -o-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}
figure .contentRight .content-r-inner {
  padding: 50px 0px 50px 0;
}
figure.animq img {
  margin-left: -10%;
  -webkit-transition-delay: 0.3s;
  -moz-transition-delay: 0.3s;
}
figure.animq .contentRight {
  right: 0;
  -webkit-transition-delay: 0.1s;
  -moz-transition-delay: 0.1s;
}
.moreImageContainer .contentRight {
  position: fixed;
}
/*
	END ANIMATIONS
*/
/*
	5.4. CONTENT
*/

figure .content {
  padding-left: 30px;
  padding-right: 30px;
  z-index: 10;
  left: 0;
  opacity: 1;
}
figure .contentsqz {
  padding-left: 30px;
  padding-right: 30px;
