  opacity: 1;
  padding-left: 0px;
  padding-right: 0px;
  position: absolute !important;
  right: 0;
  text-align: center;
  top: 0;
  width: 100%;
  z-index: 10;
}
.callpost .content {
  background: none;
  display: table;
