}
.bigerImage .content h2 {
  margin-top: 0%;
}
figure .contentsqz h2 {
  font-family: 'Lato', sans-serif;
  font-weight: 800;
  color: #FFF;
  text-transform: uppercase;
  padding-left: 0px;
  line-height: 24px;
  font-size: 22px;
