figure.mainImage .content h2 span {} figure.anim .content {
                                       opacity: 1;
                                     }
figure.anim .contentsqz {
  opacity: 1;
}
.close {
  position: absolute;
  right: 30px;
  top: 30px;
  background: url('@{image-system-path}close.png') no-repeat 0 0;
  float: left;
  display: block;
  text-decoration: none;
  text-indent: -9999px;
  overflow: hidden;
  cursor: pointer;
  width: 11px;
  height: 11px;
  z-index: 111111111;
}
/*
	END CONTENT
*/
/*
	5.5. STARTING IMAGE ANIMATION
*/

#gallery {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #000
}
#gallery img {
  width: 110%;
  margin-top: -5%;
  margin-left: -5%;
  -webkit-transition: all 0.8s ease-in-out;
  -moz-transition: all 0.8s ease-in-out;
  -o-transition: all 0.8s ease-in-out;
  transition: all 0.8s ease-in-out;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  -moz-filter: blur(5px);
  -o-filter: blur(5px);
  -ms-filter: blur(5px);
}
.animate #gallery img {
  width: 100%;
  margin: 0;
  filter: blur(0);
  -webkit-filter: blur(0);
  -moz-filter: blur(0);
  -o-filter: blur(0);
  -ms-filter: blur(0);
}
/*
	END STARTING IMAGE ANIMATION
*/
/*
	5.6. IMAGE GALLERY SCALING
*/

.scalediv.scaleshow {
  -ms-transform: scale(0.5, 0.5);
  -webkit-transform: scale(0.5, 0.5);
  transform: scale(0.5, 0.5);
  -webkit-transition: all 1s ease-in-out;
  -moz-transition: all 1s ease-in-out;
  -o-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}
.scalediv.scaleshowhide {
  display: none;
}
.scalediv.upscaleshow {
  -ms-transform: scale(1, 1);
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1);
  -webkit-transition: all 1s ease-in-out;
  -moz-transition: all 1s ease-in-out;
  -o-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}
/*
	END IMAGE GALLERY SCALING
*/
/*
END PORTFOLIO
*/
/*
6. POSTS
*/

#landingpage {
  background-image: url('@{image-system-path}menu.png');
  background-position: center center;
  background-size: cover;
}
#aboutus,
#contactus,
#blog,
.posts {
  background-color: #fff;
  height: 100%;
  width: 100%;
}

#thumbnails .scalediv {
  height: 100%;
  overflow-y: scroll;
}

#team .scalediv {
  height: 100%;
  overflow-y: scroll;
}

#blog .scalediv {
  height: 100%;
  overflow-y: scroll;
}
#aboutus .content-r-inner {
  padding: 0;
}
.posts {
  background: none;
  height: 100%;
  width: 100%;
  top: 0px;
  position: relative;
  display: none;
  -webkit-transition: all 1s ease-in-out;
  z-index: 10000000;
  position: absolute;
  background: rgba(0, 0, 0, 0);
  -moz-transition: all 1s ease-in-out;
  -o-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}
#aboutus aside,
#contactus aside {
  width: 35%;
  float: left;
  height: 100%;
  position: relative;
  overflow: hidden;
}
#blog aside,
.posts aside {
  width: 35%;
  float: left;
  height: 100%;
  position: relative;
}
.posts section,
#aboutus section,
#contactus section,
#blog section {
